@import url('https://fonts.googleapis.com/css2?family=Caveat:wght@400;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Amatic+SC:wght@400;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Amatic+SC:wght@400;700&family=Slackside+One&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Amatic+SC:wght@400;700&family=Kalnia+Glaze:wght@100..700&family=Marcellus+SC&family=Slackside+One&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Amatic+SC:wght@400;700&family=Josefin+Sans:ital,wght@0,100..700;1,100..700&family=Kalnia+Glaze:wght@100..700&family=Marcellus+SC&family=Slackside+One&display=swap');

/*
Caveat
Amatic SC
Kalnia Glaze　colorフォント
Marcellus SC
Josefin Sans　futuraっぽい

*/

@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}

.caveat-font {
font-family: 'Caveat', cursive;

}

.cursor-move {
  cursor: move;
}

.cursor-default {
  cursor: default;
}

.cursor-pointer {
  cursor: pointer;
}

.cursor-text {
  cursor: text;
}


/* スクロールバーを完全に非表示にする */
::-webkit-scrollbar {
  display: none;
}

/* Firefox用 */
* {
  scrollbar-width: none;
}

/* IE and Edge用 */
* {
  -ms-overflow-style: none;
}